<template>
  <div id="app">

    <pc v-if="IsPc"></pc>
    <!--<test v-if="IsPc"></test>-->
    <phone v-else></phone>
  </div>
</template>

<script>
  import pc from './components/pc/index'
  import test from './components/pc/test'
  import phone from './components/phone/index'
  import util from './common/js/util'

  export default {
    name: 'App',
    components: {
      pc,
      phone,
      test
    },

    created: function () {
      this.IsPc = util.IsPC()
      if(!this.IsPc)
      {
        this.initRem()
      }

    },
    methods:{
      initRem:function () {
        let htmlWidth = document.documentElement.clientWidth || document.clientWidth
        let htmlDom = document.getElementsByTagName('html')[0]
        htmlDom.style.fontSize = htmlWidth/10+'px'
      },
    }
  }
</script>

<style lang="scss">

  @import "common/scss/base";


  html, body {
    height: 100%;
  }

  body {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*text-align: center;*/
    /*color: #2c3e50;*/
    /*margin-top: 60px;*/

  }
</style>



