    display: flex;
    justify-content: center;

    .content-wrapper {
      width: $content-width;
      background-position: center;
      background-size: auto 100%;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      @include PAGE-HEIGHT;

      .title {
        height: 130px;
        font-size: 24px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 36px;
        letter-spacing: 1px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

      }
      .bottom-wrapper {
        width: 100%;
        flex: 1;

        background-color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        .coach-wrapper {
          display: flex;
          flex-direction: column;
          justify-content: center;
          .row-wrapper {
            display: flex;
            padding-top: 10px;
          }
          .item-wrapper {
            margin-right: 15px;
            display: flex;
            flex-direction: column;
            background-color: rgb(247, 247, 247);
            height: 278px;
            width: 400px;
            .coach-image-wrapper {
              display: flex;
              height: 214px;
              .coach-image {
                width: 124px;
                height: 170px;
                flex-shrink: 0;
              }
              .coach-name-wrapper {
                margin-left: 20px;
                margin-right: 20px;
                display: flex;
                flex-direction: column;
                .coach-name {
                  font-size: 20px;
                  font-weight: 600;
                  color: rgba(239, 74, 33, 1);
                  line-height: 28px;

                }
                .coach-info {
                  font-size: 16px;
                  font-weight: 500;
                  color: rgba(102, 102, 102, 1);
                  line-height: 24px;
                  width: 238px;
                }
              }
            }

            .line {
              height: 1px;
              background: rgba(204, 204, 204, 1);
              margin-right: 20px;
              margin-left: 20px;

            }

            .sport-lang-wrapper {
              display: flex;
              flex: 1;
              align-items: center;
              margin: 0 30px;

              .sport-lang {
                font-size: 16px;
                font-weight: normal;
                color: #333333;
                line-height: 29px;
              }
            }

          }
        }

      }
    }

  }

  #swiper-slide5 {
    background-color: black;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-position: center;
    background-image: url('../../common/image/pc/last.jpg');
    background-size: 100% auto;
    background-repeat: no-repeat;
    .content-wrapper {
      flex: 1;
      width: 88%;
      display: flex;
      justify-content: center;
      align-items: center;

      .cv-wrapper {
        display: flex;
        width: 88%;
        height: 203px;
        background: rgba(0, 40, 57, 0.5);
        flex-direction: column;
        justify-content: center;
        align-items: center;
        span {
          font-size: 36px;
          font-weight: normal;
          color: rgba(255, 255, 255, 1);
          line-height: 44px;
          letter-spacing: 3px;
        }

        span + span {
          font-size: 30px;
          margin-top: 16px;
          line-height: 36px;
        }
      }

    }

    .cotact-wrapper {
      background: rgba(0, 40, 57, 1);
      height: 220px;
      width: 100%;
      display: flex;
      justify-content: center;
      .left-wrapper-wrapper {
        width: 900px;
        display: flex;
        padding-top: 35px;;
        /*align-items: center;*/
      }

      .left-wrapper {

        display: flex;
        flex-direction: column;
        color: white;
        font-size: 16px;
        font-weight: 500;
        line-height: 36px;
        :nth-child(1) {
          font-size: 20px;
          font-weight: 600;
          color: rgba(255, 255, 255, 1);
          line-height: 28px;
        }
      }
      .flex1 {
        flex: 1;
      }

      .right-wrapper {
        display: flex;
        flex-direction: column;

        img {
          width: 120px;
          height: 120px;
        }
        span {
          font-size: 16px;
          font-weight: 600;
          color: rgba(255, 255, 255, 1);
          line-height: 34px;
          letter-spacing: 1px;
          width: 100%;
          display: block;
          text-align: center;
          margin-top: 6px;
        }

      }
    }

  }

  .coach-swiper-outer {
    .coach-swiper-container {
      background-color: white;
      height: 300px;
    }
  }


</style>



